body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}



.track{
  width: 250px;
  height: 250px;
  display: inline-block;
  position: relative;
  margin: 20px;
  cursor: pointer;
}

.track-img{
  width: 250px;
  height: 250px;
}

.track-name{
  background-color: black;
  width: 250px;
  color: white;
  position: absolute;
  bottom: 0;
  margin-bottom: 0;
}

.track-icon{
  position: absolute; 
  bottom: 100px;
  left: 100px;
  color: white;
  background-color: black;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  opacity: 0.8;
}
.track-icon span{
  font-size: 40px;
}